import React from "react";
import {
  Box,
  Button,
  ButtonGroup,
  Divider,
  Container,
  Grid,
} from "@mui/material";
import { styled } from "@mui/material/styles";

const Ultimate = () => {
  const GameBoard = styled(Box)(({ theme }) => ({
    display: "grid",
    gridTemplateRows: "100px 100px 100px",
    gridTemplateColumns: "100px 100px 100px",
    justifyContent: "center",
  }));

  const GameBox = styled(Box)(({ theme }) => ({
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  }));

  const [board, setBoard] = React.useState([
    ["", "", ""],
    ["", "", ""],
    ["", "", ""],
  ]);

  const reset = () => {
    setBoard((board) => [
      ["", "", ""],
      ["", "", ""],
      ["", "", "2"],
    ]);
  };

  const [currentPlayer, setCurrentPlayer] = React.useState("Player 1");

  const changePlayer = () => {
    if (currentPlayer === "Player 1") {
      setCurrentPlayer((currentPlayer) => "Player 2");
    } else {
      setCurrentPlayer((currentPlayer) => "Player 1");
    }
  };
  const changeBoard = (col: number, row: number) => {
    // let newBoard = board.map(row => [...row])
    let newBoard = JSON.parse(JSON.stringify(board));
    if (board[col][row] === "X" || board[col][row] === "Y") {
      return;
    }
    if (currentPlayer === "Player 1") {
      newBoard[col][row] = "X";
      setBoard((board) => newBoard); //WHYYY DOESN"T THIS WORK
    } else {
      //board[col][row] = "Y"; // OLD WAY
      newBoard[col][row] = "Y";
      setBoard((board) => newBoard); //WHYYY DOESN"T THIS WORK
    }
    changePlayer();
  };

  return (
    <Container>
      <Box sx={{ margin: 2 }}>Current Player : {currentPlayer} </Box>
      <GameBoard>
        <GameBox
          sx={{ borderBottom: "1px solid #766", borderRight: "1px solid #666" }}
          onClick={() => changeBoard(0, 0)}
        >
          {board[0][0]}
        </GameBox>
        <GameBox
          sx={{ borderBottom: "1px solid #766", borderRight: "1px solid #666" }}
          onClick={() => changeBoard(0, 1)}
        >
          {board[0][1]}
        </GameBox>
        <GameBox
          sx={{ borderBottom: "1px solid #766" }}
          onClick={() => changeBoard(0, 2)}
        >
          {board[0][2]}
        </GameBox>
        <GameBox
          sx={{ borderBottom: "1px solid #666", borderRight: "1px solid #666" }}
          onClick={() => changeBoard(1, 0)}
        >
          {board[1][0]}
        </GameBox>
        <GameBox
          sx={{ borderBottom: "1px solid #666", borderRight: "1px solid #666" }}
          onClick={() => changeBoard(1, 1)}
        >
          {board[1][1]}
        </GameBox>
        <GameBox
          sx={{ borderBottom: "1px solid #666" }}
          onClick={() => changeBoard(1, 2)}
        >
          {board[1][2]}
        </GameBox>
        <GameBox
          sx={{ borderRight: "1px solid #666" }}
          onClick={() => changeBoard(2, 0)}
        >
          {board[2][0]}
        </GameBox>
        <GameBox
          sx={{ borderRight: "1px solid #666" }}
          onClick={() => changeBoard(2, 1)}
        >
          {board[2][1]}
        </GameBox>
        <GameBox onClick={() => changeBoard(2, 2)}>{board[2][2]}</GameBox>
      </GameBoard>
      <Box>Board : {board}</Box>
      <Button onClick={() => reset()}>RESET</Button>
    </Container>
  );
};
export default Ultimate;
