import ResumeTab from "../AllTabs/ResumeFolder/ResumeTab.js";
import BakeryGame from "../AllTabs/Bakery_Game/BakeryGame.tsx";
import OptionsTab from "../AllTabs/OptionsTab.js";
import Ultimate from "../AllTabs/TikTakToe/Ultimate.tsx";
import "../../App.css";

import * as React from "react";
import PropTypes from "prop-types";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import { Box, Button, Typography } from "@mui/material";

import LandingPage from "./LandingPage";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Box
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </Box>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `tab-${index}`,
    "aria-controls": `tabpanel-${index}`,
  };
}

export default function BasicTabs({ selectTheme, themeOptions }) {
  const [value, setValue] = React.useState(0);
  const [isToggled, setIsToggled] = React.useState(false);

  const toggle = React.useCallback(
    () => setIsToggled(!isToggled),
    [isToggled, setIsToggled]
  );

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box>
      {isToggled ? (
        <Box>
          <Box sx={{ borderColor: "divider" }}>
            <Tabs
              value={value}
              onChange={handleChange}
              aria-label="myTabs"
              centered
            >
              <Tab label="Bryce Sharp" {...a11yProps(0)} />
              <Tab label="Bakery Game" {...a11yProps(1)} />
              <Tab label="Ultimate TTT (WIP)" {...a11yProps(2)} />
              <Tab label="Options" {...a11yProps(3)} />
            </Tabs>
          </Box>
          <TabPanel value={value} index={0}>
            <ResumeTab />
          </TabPanel>
          <TabPanel value={value} index={1}>
            <BakeryGame />
          </TabPanel>
          <TabPanel value={value} index={2}>
            <Ultimate />
          </TabPanel>
          <TabPanel value={value} index={3}>
            <OptionsTab selectTheme={selectTheme} themeOptions={themeOptions} />
            <Button onClick={toggle}> Return to Landing Page </Button>
          </TabPanel>
        </Box>
      ) : (
        <LandingPage toggled={toggle} />
      )}
    </Box>
  );
}
