import * as React from "react";
import { Button, Fab, Box, Container, Paper, Typography } from "@mui/material";

const OptionsTab = ({ selectTheme, themeOptions }) => {
  return (
    <Container className="OptionsTab">
      <p> Options Pannel </p>
      <Box sx={{ "& > :not(style)": { m: 1 } }}>
        {themeOptions.map((themeOption) => {
          return (
            <Fab
              size="large"
              sx={{
                backgroundColor: themeOption.pColor,
                "&:hover": { backgroundColor: themeOption.dColor },
              }}
              onClick={() => selectTheme(themeOption.value)}
            >
              {themeOption.label}
            </Fab>
          );
        })}
      </Box>
      <Button variant="contained">Primary</Button>
      <Button color="secondary" variant="contained">
        Secondary
      </Button>
    </Container>
  );
};
export default OptionsTab;
