import "./App.css";
import React from "react";
import Tabs from "./Components/TabComponent/Tabs";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { red, teal, amber, grey, blueGrey } from "@mui/material/colors/";
import { Box, Paper } from "@mui/material";
import "@fontsource/roboto/300.css";
import "@fontsource/roboto/400.css";
import "@fontsource/roboto/500.css";
import "@fontsource/roboto/700.css";

const theme_teal_primary = teal[200];
const theme_teal_secondary = teal[500];
const theme_teal_dark = "#4f9a94";
const theme_teal_bg = grey[100];

const theme_red_primary = red[200];
const theme_red_secondary = red[500];
const theme_red_dark = "#ba6b6c";
const theme_red_bg = grey[100];

const theme_amber_primary = amber[300];
const theme_amber_secondary = amber[600];
const theme_amber_dark = "#c8a415";
const theme_amber_bg = grey[100];

const themeOptions = [
  // Just used for sending the options back
  {
    label: "Red",
    value: "theme_red",
    pColor: theme_red_primary,
    sColor: theme_red_secondary,
    dColor: theme_red_dark,
    bgColog: theme_red_bg,
  },
  {
    label: "Teal",
    value: "theme_teal",
    pColor: theme_teal_primary,
    sColor: theme_teal_secondary,
    dColor: theme_teal_dark,
    bgColog: theme_teal_bg,
  },
  {
    label: "Amber",
    value: "theme_amber",
    pColor: theme_amber_primary,
    sColor: theme_amber_secondary,
    dColor: theme_amber_dark,
    bgColog: theme_amber_bg,
  },
];

function App() {
  const [colorMode, setColorMode] = React.useState("dark");
  const theme_teal = React.useMemo(
    () =>
      createTheme({
        palette: {
          primary: { main: theme_teal_primary, dark: theme_teal_dark },
          secondary: { main: theme_teal_secondary },
          custom: { main: theme_teal_bg },
          mode: colorMode,
        },
      }),
    [colorMode]
  );

  const theme_red = createTheme({
    palette: {
      primary: { main: theme_red_primary, dark: theme_red_dark },
      secondary: { main: theme_red_secondary },
      custom: { main: theme_red_bg },
      mode: colorMode,
    },
  });

  const theme_amber = createTheme({
    palette: {
      primary: { main: theme_amber_primary, dark: theme_amber_dark },
      secondary: { main: theme_amber_secondary },
      custom: { main: theme_amber_bg },
      mode: colorMode,
    },
  });

  const [theme, setTheme] = React.useState(theme_amber);

  const selectTheme = (newTheme) => {
    if (newTheme === "theme_teal") {
      setTheme(theme_teal);
    }
    if (newTheme === "theme_red") {
      setTheme(theme_red);
    }
    if (newTheme === "theme_amber") {
      setTheme(theme_amber);
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Paper>
        <Box className="App">
          <Tabs selectTheme={selectTheme} themeOptions={themeOptions} />
        </Box>
      </Paper>
    </ThemeProvider>
  );
}
export default App;
